import React, { Component } from 'react'
import { signIn } from '../../api/auth/cognito'


class LoginForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      username: '',
      email: '',
      password: '',
    }
  }

  _handleFieldChange = (field, event) => {
    this.setState({ [field]: event.target.value })
    console.log('>> this.state', this.state)
  }

  _handleClickSearch = () => {
    console.log(
      '>> SIGNUP',
      this.state.username, this.state.password
    )
    const { username, password } = this.state
    signIn(username, password, this._onSuccessfulSignUp)
  }

  _onSuccessfulSignUp = () => {
    '>> SUCCESSSFULLY SIGNED UP!'
  }

  _onKeyPress = (event) => {
    if (event.key === 'Enter') {
      this._handleClickSearch()
    }
  }

  render() {
    return (
      <div className={'loginForm__wrapper'} onKeyPress={this._onKeyPress}>
        {'SIGN UP'}
        <form onSubmit={this._handleClickSearch}>
          <input
            onChange={(event) => this._handleFieldChange('username', event)}
            type={'text'}
            placeholder={'Username'}
            className={'signUpFormInput'}
          />
          <input
            onChange={(event) => this._handleFieldChange('email', event)}
            type={'text'}
            placeholder={'Email'}
            className={'signUpFormInput'}
          />
          <input
            onChange={(event) => this._handleFieldChange('password', event)}
            type={'password'}
            placeholder={'Password'}
            className={'signUpFormInput'}
          />
          <input type={'submit'} value={'submit'} />
        </form>
      </div>
    )
  }
}

export default LoginForm
